import React from 'react';
import styles from './Projects.module.css';
import Project from "./Project/Project";
import Tittle from "../Common/Title";
import urlSN from "../Common/Assets/SN.jpg";
import urlToDoList from "../Common/Assets/toDolist.jpg";
import Fade from 'react-reveal/Fade';


function Projects() {

    const backGroundImgSN = {
        backgroundImage: `url(${urlSN})`,
        backgroundSize: 'cover',
        backgroundPosition: 'center'
    };
    const backGroundImgToDoList = {
        backgroundImage: `url(${urlToDoList})`,
        backgroundSize: '70%',
        backgroundPosition: 'center'
    };
    return (
        <div id='Projects' className={styles.projectsBlock}>
            <div className={styles.container}>
                <Fade right>
                    <Tittle title={'Проекты'}/>
                </Fade>
                <div className={styles.projects}>
                    <Fade right>
                        <Project title={'Социальная сеть'}
                                 description={'Соцсеть сделана с помощью технологий: React + Redux. ' +
                                 'В качестве API используется https://social-network.samuraijs.com/'}
                                 backgroundImg={backGroundImgSN}/>
                    </Fade>
                    <Fade right>
                        <Project title={'Список дел'}
                                 description={'Список дел выполнен при помощи стака технологий: React + Redux и ' +
                                 'CSS фреймворка Bulma'}
                                 backgroundImg={backGroundImgToDoList}/>
                    </Fade>
                </div>
            </div>
        </div>
    );
}

export default Projects;
