import React from 'react';
import styles from './Contacts.module.css';
import Tittle from "../Common/Title";
import Fade from 'react-reveal/Fade';
import Btn from "../Common/Btn";



function Contacts() {
    const color = {
        color: `#ffffff`
    };
    return (
        <div id={'contacts'} className={styles.contactsBlock}>
            <div className={styles.container}>
                <Fade bottom>
                <Tittle title={'Контакты'}/>
                <form className={styles.form}>
                    <input className={styles.formArea} placeholder='Введите свое имя'/>
                    <input className={styles.formArea} placeholder='Ввдите свой email'/>
                    <textarea className={styles.messageArea} placeholder='Введите сообщение'/>
                </form>
                <p></p>
                {/*<Btn title={'Отправить'}/>*/}
               {/* <button className={styles.btnYo} type='submit'>Отправить</button>*/}
                    <Btn title={'Отправить'} color={color}/>
                </Fade>
            </div>
        </div>
    );
}

export default Contacts;
